import React from "react";
import MainSearch from "components/main/MainSearch";
import MainSlider from "components/main/MainSlider";
import { Button, Stack, Link, Box } from "@mui/material";
import Container from "@mui/material/Container";
import SubTitle from "components/common/SubTitle";
import XgolfViewHistory from "components/main/XgolfViewHistory";
import { useNavigate } from "react-router-dom";

function Main() {
  const navigate = useNavigate();
  return (
    <>
      <Container maxWidth="sm" id="container">
        {/*  */}
        <Box className="section">
          <Stack spacing={1}>
            <MainSearch />
            <Stack direction="row" spacing={1}>
              <Button fullWidth onClick={() => navigate("/list/type1")}>
                 
              </Button>
              <Button fullWidth onClick={() => navigate("/list/type2")}>
                 
              </Button>
            </Stack>
            <Stack direction="row" spacing={1}>
              <Link href="#"># </Link>
              <Link href="#"># </Link>
            </Stack>
          </Stack>
        </Box>
        <Box className="section">
          <SubTitle> </SubTitle>
          <MainSlider />
        </Box>

        {/*    */}
        <XgolfViewHistory />

        <Box className="section">
          <SubTitle> </SubTitle>
          <Button
            variant="contained"
            fullWidth
            color="green"
            size="large"
            sx={{ mt: 1 }}
            onClick={() => navigate("/add")}
          >
               
          </Button>
        </Box>
      </Container>
    </>
  );
}

export default Main;
